import React, { Component } from 'react';
import { Link, NavLink} from 'react-router-dom';
import { IntlActions } from 'react-redux-multilingual'
import Pace from 'react-pace-progress'

// Import custom components
import store from '../../../store';
import NavBar from "./common/navbar";
import SideBar from "./common/sidebar";
import CartContainer from "./../../../containers/CartContainer";
import TopBar from "./common/topbar";
import LogoImage from "./common/logo";
import {changeCurrency, searchSingle} from '../../../actions'
import {connect} from "react-redux";
import {Row, Col, Container, Alert} from "react-bootstrap";
import { Redirect } from "react-router-dom";
import {getVisibleproducts} from "../../../services";
import CustomNavBar from "./common/CustomNavBar";

class HeaderOne extends Component {

    constructor(props) {
        super(props);

		this.state = {
			isLoading:false,
            navOpened: false,
            search: "",
		}
    }
    /*=====================
         Pre loader
         ==========================*/
    componentDidMount() {
        setTimeout(function() {
            document.querySelector(".my-loader-wrapper").style = "display: none";
        }, 2000);

        this.setState({ open: true });
    }

    componentWillMount(){
        window.addEventListener('scroll', this.handleScroll);
	}
    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll = () => {
        let number = window.pageXOffset || document.documentElement.scrollTop || document.body.scrollTop || 0;
        if (number >= 250) {
            if (window.innerWidth < 576) {
 //               const header = document.getElementById("sticky");


            }else {
 //               const header = document.getElementById("sticky");
 //               header.classList.add('fixed');


            }

        } else {
   //         const header = document.getElementById("sticky");
   //         header.classList.remove('fixed');
        }
    }

    changeLanguage(lang) {
        store.dispatch(IntlActions.setLocale(lang))
	}

    openNav() {
        var openmyslide = document.getElementById("mySidenav");
        if(openmyslide){
            openmyslide.classList.add('open-side')
		}
    }

    openSearch() {
        document.getElementById("search-overlay").style.display = "block";
    }

    closeSearch() {
        document.getElementById("search-overlay").style.display = "none";
    }

	load = ()=>{
		this.setState({isLoading: true});
		fetch().then(()=>{
			// deal with data fetched
			this.setState({isLoading: false})
		})
	};

    handleOpenNav = (value) => {
        this.setState({navOpened: value})
    }

    handleSearch = (e) => {

        this.closeSearch();
        e.preventDefault();

    }

	render() {

		return (
			<div>
                <div style={{maxWidth:"100%", padding:0, margin:0, borderRadius:0, backgroundColor:"#90EE90", marginBottom:0, paddingBottom:0, paddingTop:0}} onClose={() => console.log("close")}>
                    <Link to={`/collections/winter`}>
                        <p style={{color:"black", fontWeight:"100", padding:10, marginTop:0, marginBottom:0, fontFamily:"Poppins", fontSize:14, textAlign:"center"}}>
                            CYBER MONDAY ALL WEEK! 50% OFF!
                        </p>
                    </Link>
                </div>
                <div style={{maxWidth:"100%", padding:0, margin:0, borderRadius:0, backgroundColor:"#F19CBB", marginBottom:15, paddingBottom:0, paddingTop:0}} onClose={() => console.log("close")}>
                    <Link to={`/collections/winter`}>
                        <p style={{color:"black", fontWeight:"300", padding:10, marginBottom:0, marginTop:0, fontFamily:"Poppins", fontSize:14, textAlign:"center"}}>
                            Winter Sale + Free Shipping Over $35!
                        </p>
                    </Link>
                </div>
				<header id="sticky" className="sticky">
					{this.state.isLoading ? <Pace color="#27ae60"/> : null}


					{/*Top Header Component*/}
					<TopBar/>
					<div className="">
                        <Row sm={12} style={{marginLeft:10, marginRight:10}}>

                        </Row>

                            <div className="main-menu">

                                <Row style={{display:"flex", flex:2,  alignItems:"center", justifyContent:"center"}}>
                                    <Col className="menu-left" style={{flex:1, marginLeft:10}}>
                                        <div className="brand-logo" id={"brand-logo"}>
                                            <LogoImage logo={this.props.logoName} />
                                        </div>
                                    </Col>
                                    <div className="menu-right pull-right">
                                        {this.props.window.width < 1185 &&
                                            <Col style={{flex: 2, display:"flex", position:"relative", alignItems:"flex-end", justifyContent:"center"}}>
                                                <NavBar navOpened={this.state.navOpened} setNav={(state) => this.handleOpenNav(state)}/>
                                            </Col>

                                        }
                                    </div>

                                    <Col style={{flex:1, display:"flex", alignItems:"center", marginRight:5, justifyContent:"flex-end",position:"relative"}}>
                                        <div className="menu-right pull-right">
                                            <div className="icon-nav" >
                                                <ul style={{flex:1, display:"flex", flexWrap:"nowrap"}}>
                                                    <li className="onhover-div mobile-search" style={{flex:1, flexShrink:0, paddingLeft:0, paddingRight:0,   zIndex:5}}>
                                                        <div><img src={`/assets/images/icon/search.png`} style={{}} onClick={this.openSearch} className="img-fluid" alt="" />
                                                            <i className="fa fa-search" onClick={this.openSearch}></i></div>
                                                    </li>
                                                    {/*Header Cart Component */}
                                                    <CartContainer/>
                                                    {
                                                        this.props.window.width < 1200 &&
                                                        <li  style={{flex:1, flexShrink:0, paddingLeft:0, marginTop:"auto", marginBottom:"auto", paddingTop:10,zIndex:1}}>
                                                            <div className="toggle-nav" style={{}} onClick={() => this.handleOpenNav(true)} >
                                                                <i className="fa fa-bars sidebar-bar" style={{maxWidth:"250px", marginTop:10}}></i>
                                                            </div>
                                                        </li>
                                                    }


                                                </ul>
                                            </div>
                                        </div>
                                    </Col>
                                </Row>


                            </div>

					</div>

				</header>

                <div id="search-overlay" className="search-overlay">
                    <div>
                        <span className="closebtn" onClick={this.closeSearch} title="Close Overlay">×</span>
                        <div className="overlay-content">
                            <div className="container">
                                <div className="row">
                                    <div className="col-xl-12">
                                        <form onSubmit={(e) => this.handleSearch(e)}>
                                            <div className="form-group">
                                                <input type="text" className="form-control" onSubmit={this.closeSearch} onChange={(e) => this.setState({search: e.target.value})} id="exampleInputPassword1" placeholder="Search a Product" />
                                            </div>
                                            <Link to={`/search/${this.state.search}`}>
                                                <button type="submit" className="btn btn-primary" onClick={() => this.closeSearch()}><i className="fa fa-search"></i></button>
                                            </Link>

                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <CustomNavBar navOpened={this.state.navOpened} setNav={(state) => this.handleOpenNav(state)}/>
			</div>
			)
	}
}
const mapStateToProps = (state) => ({
    window: state.window.window

})


export default connect(
    mapStateToProps,
    { changeCurrency }
)(HeaderOne);
