import {
    FETCH_SINGLE_PRODUCT,
    CHANGE_CURRENCY,
    RECEIVE_PRODUCTS, ADD_PRODUCTS_TO_RECEIVE, SET_PRODUCTS
} from "../constants/ActionTypes";


const initialState = {
    products: [],
    symbol: '$',
    product_details: []
};

const productReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_PRODUCTS:
            //this is probably not the best way of doing this.
            let newProducts;
            if (state.products.length > 0) {
                const uniqueProducts = []
                const checkIds = []
                for (let i=0; i < state.products.length; i++) {
                    checkIds.push(state.products[i].id)
                }
                const productsToAdd = action.products
                for (let i=0; i < productsToAdd.length; i++) {

                    if (checkIds.indexOf(productsToAdd[i].id) === -1) {
                        uniqueProducts.push(productsToAdd[i])
                    }
                }
                newProducts = state.products.concat(uniqueProducts)
            }
            else newProducts = action.products;

            return {...state,
                products: newProducts
            }
        case FETCH_SINGLE_PRODUCT:
            if (state.products.findIndex(product => product.id === action.productId) !== -1) {
                const singleItem = state.products.reduce((itemAcc, product) => {
                    return product
                }, [])
                return { ...state,
                    product_details: singleItem };
            }

        case CHANGE_CURRENCY:
            return { ...state,
                symbol: action.symbol };
        default:
            return state;
    }
};
export default productReducer;
