import React, {Component} from 'react';
import { Link} from 'react-router-dom';

import {SlideUpDown} from "../../../services/script"
import LogoImage from "../headers/common/logo"
import ReviewSlider from "../../layouts/fashion/ReviewSlider";

class FooterOne extends Component {

    componentDidMount(){
        var contentwidth = window.innerWidth;
        if ((contentwidth) < 750) {
            SlideUpDown('footer-title');
        } else {
            var elems = document.querySelectorAll(".footer-title");
            [].forEach.call(elems, function(elemt) {
                let el = elemt.nextElementSibling;
                el.style = "display: block";
            });
        }
    }


    render () {

        return (
            <footer className="footer-light">
                <ReviewSlider/>
                <section className="section-b-space light-layout">
                    <div className="container">
                        <div className="row footer-theme partition-f">
                            <div className="col-lg-4 col-md-6">
                                <div className="footer-title footer-mobile-title">
                                    <h4>about</h4>
                                </div>
                                <div className="footer-contant">
                                    <div className="footer-logo">
                                        <LogoImage logo={this.props.logoName} />
                                    </div>
                                    <p>For over 25 years, Pretty Girl Stores have offered incredible stylish clothes and striking modern looks for the budget minded fashionista. Serving our customers throughout the Tri-State area, Pretty Girl prides itself on taking fast fashion to the next level and offering women's clothing at incredibly low prices. </p>
                                </div>
                            </div>

                            <div className="col offset-xl-1">
                                <div className="sub-title">
                                    <div className="footer-title">
                                        <h4>Details</h4>
                                    </div>
                                    <div className="footer-contant">
                                        <ul>
                                            <li><a style={{pointer:"cursor"}} href={`https://pretty-girl-stores.myshopify.com/pages/contact-us`} >Contact Us</a></li>
                                            <li><a style={{pointer:"cursor"}} href={`https://pretty-girl-stores.myshopify.com/pages/shipping`} >Shipping</a></li>
                                            <li><a style={{pointer:"cursor"}} href={`https://prettygirlstores.returnscenter.com/`} >Returns</a></li>
                                        </ul>
                                        <h4 style={{marginTop:15, fontSize:"1rem", marginBottom:10}}>Stay Connected</h4>
                                        <div className="footer-social" style={{marginTop:10}}>
                                            <a href={'https://www.facebook.com/PrettyGirlStoresOfficial/'} style={{marginRight:5, marginLeft:5}} ><i className="fa fa-facebook" aria-hidden="true"></i></a>
                                            <a href={'https://www.instagram.com/prettygirlstoresofficial/'}><i className="fa fa-instagram" aria-hidden="true" style={{marginRight:5, marginLeft:5}}></i></a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col">
                                <div className="sub-title">
                                    <div className="footer-title">
                                        <h4>store information</h4>
                                    </div>
                                    <div className="footer-contant">
                                        <ul className="contact-list">
                                            <li>Pretty Girl Stores
                                                , 203 E Fordham Rd, The Bronx, NY 10458
                                            </li>
                                            <li>Text "Yes" to :(+1 270-972-4755)</li>
                                            <li>Email Us: <a
                                                href="#">Support@prettygirlstores.com</a></li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                <div className="sub-footer ">
                    <div className="container">
                        <div className="row">
                            <div className="col-xl-6 col-md-6 col-sm-12">
                                <div className="footer-end">
                                </div>
                            </div>
                            <div className="col-xl-6 col-md-6 mt-3 col-sm-12">
                                <div className="payment-card-bottom">
                                    <ul>
                                        <li>
                                            <a href="#"><img src={`/assets/images/icon/visa.png`} alt="" /></a>
                                        </li>
                                        <li>
                                            <a href="#"><img src={`/assets/images/icon/mastercard.png`} alt="" /></a>
                                        </li>
                                        <li>
                                            <a href="#"><img src={`/assets/images/icon/paypal.png`} alt="" /></a>
                                        </li>
                                        <li>
                                            <a href="#"><img src={`/assets/images/icon/american-express.png`} alt="" /></a>
                                        </li>
                                        <li>
                                            <a href="#"><img src={`/assets/images/icon/discover.png`} alt="" /></a>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </footer>
        )
    }
}

export default FooterOne;
