import * as types from '../constants/ActionTypes'


const clientReducerDefaultState = {
    client: []
};

const client = (state = clientReducerDefaultState, action) => {
    switch (action.type) {
        case types.ACCESS_CLIENT_OBJECT:
            return {
                ...state,
                client: action.client
            };
        default:
            return state;
    }
}

export default client;
