import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import Modal from 'react-responsive-modal';
import moment from "moment";
import _ from 'lodash';
import { Row, Col } from "react-bootstrap";
import {Animated} from 'react-animated-css';
import {findVariantAndSize, getBestSeller, getUnderFifteen, getVisibleproducts, getWomensWear} from "../../../services";
import plusSymbol from "../../../assets/images/plus.svg";
import {connect} from "react-redux";
import {addToCart, addToCompare, addToWishlist} from "../../../actions";
import ReactGA from "react-ga";

class ProductItem extends Component {

    constructor(props){
        super(props)

        this.state = {
            open: false,
            stock: 'In Stock',
            quickAdd: false,
            quickBuy: false,
            quantity: 1,
            image: null,
            product: null,
            variant: null,
            size: 0
        }
    }

    componentDidMount() {
        const variant = this.props.product.variants

        this.setState({product: this.props.product, quickBuy: false, variant: variant[0], quickAdd: false})
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.product.id !== this.state.product.id) {
            this.setState({product: this.props.product, variant: this.props.product.variants[0], size:0, image: null, quickAdd:false, quickBuy:false})
        }
    }

    onOpenModal = () => {
        this.setState({ open: true });
    };

    onCloseModal = () => {
        this.setState({ open: false });
    };

    minusQty = () => {
        if(this.state.quantity > 1) {
            this.setState({stock: 'In Stock'})
            this.setState({quantity: this.state.quantity - 1})
        }
    }

    plusQty = () => {
        if(this.props.product.stock >= this.state.quantity) {
            this.setState({quantity: this.state.quantity+1})
        }else{
            this.setState({stock: 'Out of Stock !'})
        }
    }
    changeQty = (e) => {
        this.setState({ quantity: parseInt(e.target.value) })
    }


    returnMainIMG = (product) => {
        if (this.state.image) {
            return this.state.image;
        }
        else {
            this.setState({image: product.variants[0].image.src})
            if (product.variants.length) {
                if (product.variants[0].image.src) {
                    return product.variants[0].image.src
                }
                else {
                    try {
                        if (product.images[0].src) {
                            return product.images[0].src
                        }
                    }
                    catch {
                        console.log("item " + product.title + " has no image or variant image. rendering nothing")
                        return ""
                    }
                }
            }
        }

    }

    checkIfNew = () => {
        try {
            const createdAt = moment(Date.now());
            const difference = createdAt.diff(this.props.product.createdAt, "hours");
            if (difference < 1344)
                return (<span style={{zIndex:2}} className="lable3">new</span>);
            else return "";
        }
        catch (e){
            console.log(e);
        }
    }

    checkIfSale = () => {
        try {
            const salePrice = this.props.product.variants[0].price;
            const fullPrice = this.props.product.variants[0].compareAtPrice;

            if (!fullPrice) return "";

            const discount = (100 - (salePrice/fullPrice * 100) );

            if (discount > 5)
                return (
                    <div style={{position:"absolute", top:0, left:0, zIndex:1, right:0,backgroundColor:"purple", borderBottomLeftRadius:"10px", borderBottomRightRadius:"10px"}}>
                        <p style={{textAlign:"center", color:"white", marginBottom:0, paddingTop:5, paddingBottom:5}}>{Math.round(discount)}% off!</p>
                    </div>
                )
        }
        catch(e) {
            console.log(e);
        }
    }

    referenceSizeChart = (type) => {
        const number = parseInt(type)
        if (number) return number;

        let newType = this.fixSize(type).toLowerCase().trim();
        switch (newType) {
            case "small":
                return "sm";
            case "medium":
                return "md";
            case "large":
                return "lg";
            case "x-large":
                return "xl";
            case "one size":
                return "n/a"
        }
    }

    fixSize = (size) => {
        if (size.includes("/")) {
            return size.split("/")[0]
        }
        else return size;
    }


    renderSizes = () => {
        const product = this.state.product;
        const variant = this.state.variant
        const variantSizes = [];

        if (product) {
            const findSelectedColor = _.find(variant.selectedOptions, (item) => item.name === "Color")

            if (product.variants.length) {
                for (let i=0; i < product.variants.length; i++) {
                    const variantColor = _.find(product.variants[i].selectedOptions, (item) => item.name === "Color")

                    if (!variantColor) {
                        const variantSize = _.find(product.variants[i].selectedOptions, (item) => item.name === "Size")
                        if (!variantSize) continue // this means there are no sizes on this item.
                        if (product.variants[i].available) {
                            variantSizes.push(variantSize.value)
                        }

                    }
                    else if (variantColor.value === findSelectedColor.value) {
                        const variantSize = _.find(product.variants[i].selectedOptions, (item) => item.name === "Size")

                        if (product.variants[i].available) {
                            variantSizes.push(variantSize.value)
                        }
                    }
                    else {
                        continue;
                    }

                }
            }
        }

        return (
            <Animated style={{ position:"absolute", bottom:"30%", left:0, right:0}} animateOnMount={false}
                      animationIn="fadeInRight" animationOut="fadeOutRight" isVisible={this.state.quickBuy}
            >
                <div className={"mx-auto quickActions"} style={{width:"60%"}}>
                    <p style={{padding:15, textAlign:"center", color:"black"}}>Choose Your Size</p>
                    <Col xs={10} style={{paddingBottom:15, marginLeft:"auto", marginRight:"auto"}}>
                        <Row style={{justifyContent:"center", flex:1, display:"flex", alignItems:"center"}}>

                            {   variantSizes.length > 0 ?
                                variantSizes.map((size, index) =>
                                <div className={"quickBuySize"} key={index} onClick={() => this.handleAddToCart(this.fixSize(size))}
                                     style={{
                                         width: 30,
                                         height: 30,
                                         overflow: "hidden",
                                         margin: 5,
                                         borderWidth: 1,
                                         borderRadius: 360,
                                         borderColor: "black",
                                         borderStyle: "solid",
                                         display: "flex",
                                         alignItems: "center",
                                         justifyContent: "center",
                                     }}
                                >
                                    <li>
                                        <a
                                            style={{padding: 10, color: "black"}}
                                            href={null}>
                                            {this.referenceSizeChart(size)}
                                        </a>
                                    </li>
                                </div>)
                                :
                                <div>
                                    <p style={{color:"red"}}>Sorry! This item is out of stock!</p>
                                </div>

                            }
                        </Row>
                    </Col>
                </div>

            </Animated>
        )
    }

    onClickHandle(image, variant) {
        this.setState({ image : image, variant: variant} );
    }

    renderVariants = (product) => {
        const variantList = [];
        const uniqueList = [];

        for (let a=0; a < product.variants.length; a++) {
            const value = product.variants[a].image.src
            if (!product.variants[a].available) continue
            if (uniqueList.length > 2) break;
            if (uniqueList.indexOf(value) !== -1) continue

            uniqueList.push(value)

            const temp = (
                <li className={`grid_thumb_img ${(product.variants[a].image.src === this.state.image) ? 'active' : ''}`}
                    key={a}>
                    <a href="javascript:void(0)" title="Add to Wishlist">
                        <img src={`${product.variants[a].image.src}`}
                             onClick={() => this.onClickHandle( value, product.variants[a])} style={{zIndex:2}}/>
                    </a>
                </li>
            )
            variantList.push(temp)

        }
        return variantList
    }

    handleAddToCart = async (size) => {
        this.setState({quickBuy: false, quickAdd:false})
        const product = this.props.product;
        const variant = this.state.variant;
        if (product) {
            if (product.title) {
                ReactGA.event({
                    category: 'User',
                    action: product.title,
                    label: "Add To Cart"
                });
            }
        }

        let selectedColor;
        let selectedSize = size;

        if (selectedSize.includes("/")) {
            selectedSize = selectedSize.split("/")[0];
        }

        //get variant value
        try {
            for await (const options of variant.selectedOptions) {
                if (options.name === "Color") {
                    selectedColor = options.value;
                }
            }
        }
        catch {}

        let endProduct = null;
        product.variants.forEach(item => {
            const searchColor = _.find(item.selectedOptions, (item) => item.name === "Color")
            const searchSize = _.find(item.selectedOptions, (item) => item.name === "Size")

            if (!searchColor) {
                if (!searchSize) {
                    console.log("No color or sizes exist within this item!")
                    endProduct = product
                    return product;
                }
                else {
                    let newSearchSize = searchSize.value
                    if (searchSize.value.includes("/")) {
                        newSearchSize = searchSize.value.split("/")[0];
                    }
                    if (newSearchSize === size) {
                        console.log("size match found//no colors exists with this item.")
                        endProduct = item;
                        return item;
                    }
                    else {
                        let newSearchSize = searchSize.value
                        if (searchSize.value.includes("/")) {
                            newSearchSize = searchSize.value.split("/")[0];
                        }
                        console.log(searchColor)
                        if (newSearchSize === size) {
                            console.log("Good News! We found your item!")
                            console.log("item id is" + item.id)
                            endProduct = item;
                            return item;
                        }
                    }
                }
            }
            else {
                if (!searchSize) {
                    console.log("No sizes exist within this item!")
                    if (searchColor.value === selectedColor) {
                        endProduct = item;
                        return item;
                    }
                }
                else {
                    let newSearchSize = searchSize.value
                    if (searchSize.value.includes("/")) {
                        newSearchSize = searchSize.value.split("/")[0];
                    }

                    if (searchColor.value === selectedColor && newSearchSize === size) {
                        console.log("Good News! We found your item!")
                        console.log("item id is" + item.id)
                        endProduct = item;
                        return item;
                    }
                }
            }
        })

        console.log("search terms are :: " + "Variant : " + selectedColor + " Size :: " + selectedSize)

        console.log(endProduct)
        if (!endProduct) {
            console.log("error at")
            console.log(product)
        }
        else {
            this.props.onAddToCartClicked(endProduct, this.state.quantity, this.props.client)
        }

    }

    quickActions = () => {
        return (
            <div style={{display:"flex", zIndex:55, position:"absolute", bottom:10, flex:1, height:30, width:"100%"}}>
                <Animated style={{display:"flex", width:"60%", marginLeft:"auto", marginRight:"auto",justifyContent:"flex-end", alignItems:"flex-end"}}
                          animationIn="fadeInUp" animationOut="fadeOutDown" isVisible={true}>
                    <Animated style={{flex:1, display:"flex",justifyContent:"flex-end", alignItems:"flex-end"}}
                              animationIn="fadeInUp" animationOut="fadeOutDown" isVisible={this.state.quickAdd}>
                        <Row className="quickActions" onClick={() => {this.setState({quickBuy:true, quickAdd:false})}} style={{flex:1, cursor:"pointer", justifyContent:"center", alignItems:"center"}}>
                            <p className="" style={{textAlign:"center", color:"black", fontWeight:"bold", fontFamily:"Poppins", fontSize:16, flex:5, marginTop:15}}>
                                Quick Add
                                <span>
                                    <img className="" src={plusSymbol} style={{marginLeft:25, width:25, height:25}}/>
                                </span>
                            </p>

                        </Row>
                    </Animated>
                </Animated>
            </div>
        )
    }

    checkQuickActions = (type) => {
        if (type === "enter") {
            if (this.props.window.width > 1024) {
                this.setState({quickAdd:true, quickBuy: false})
            }
        }
        else {
            if (this.props.window.width > 1024) {
                this.setState({quickBuy: false, quickAdd: false})
            }
        }
    }

    boxBreakpoints = () => {
        const width = this.props.window.width;
        if (this.props.special) return "100%"
        if (width < 320) {
            return "70%"
        }
        if (width > 320 && width < 480) {
            return "75%"
        }

        if (width > 480 && width < 768) {
            return "80%"
        }

        if (width > 768 && width < 1025) {
            return "90%"
        }


        if (width > 1025 && width < 2000) {
            return "100%"
        }
        if (width > 2000) {
            return "100%"
        }
    }

    imgBreakPoints = (product) => {
        const width = this.props.window.width;

        if (width <= 320) {
            return (
                <Link to={`/product/${product.id}`} >
                    <img
                        src={`${this.returnMainIMG(product)}`}
                        style={{width:"100%", height: 180, objectFit:"cover"}}
                        className="img-fluid"
                        alt="" />

                </Link>
            )
        }
        if (width > 320 && width <= 480) {
            return (
                <Link to={`/product/${product.id}`} >
                    <img
                        src={`${this.returnMainIMG(product)}`}
                        style={{width:"100%", height: 320, objectFit:"cover"}}
                        className="img-fluid"
                        alt="" />

                </Link>
            )
        }

        if (width > 480 && width <= 768) {
            return (
                <Link to={`/product/${product.id}`} >
                    <img
                        src={`${this.returnMainIMG(product)}`}
                        style={{width:"100%", height: 380, objectFit:"cover"}}
                        className="img-fluid"
                        alt="" />

                </Link>
            )
        }

        if (width > 768 && width <= 1023) {
            return (
                <Link to={`/product/${product.id}`} >
                    <img
                        src={`${this.returnMainIMG(product)}`}
                        style={{width:"100%", height: 420, objectFit:"cover"}}
                        className="img-fluid"
                        alt="" />

                </Link>
            )
        }


        if (width > 1023 && width <= 2000) {
            return (
                <Link to={`/product/${product.id}`} >
                    <img
                        src={`${this.returnMainIMG(product)}`}
                        style={{width:"100%", height: 450, objectFit:"cover"}}
                        className="img-fluid"
                        alt="" />

                </Link>
            )
        }
        if (width > 2000 && width <= 3000) {
            return (
                <Link to={`/product/${product.id}`} >
                    <img
                        src={`${this.returnMainIMG(product)}`}
                        style={{width:"100%", height: 480, objectFit:"cover"}}
                        className="img-fluid"
                        alt="" />
                </Link>
            )
        }
        return (
            <Link to={`/product/${product.id}`} >
                <img
                    src={`${this.returnMainIMG(product)}`}
                    style={{width:"100%", height: 480, objectFit:"cover"}}
                    className="img-fluid"
                    alt="" />

            </Link>
        )
    }



    render() {
        const {product, symbol, onAddToCartClicked, onAddToWishlistClicked, onAddToCompareClicked, client} = this.props;

        let RatingStars = []
        for(var i = 0; i < product.rating; i++) {
            RatingStars.push(<i className="fa fa-star" key={i}></i>)
        }
        return (
                <div className="product-box mx-auto"  onMouseEnter={() => this.checkQuickActions("enter")} onMouseLeave={() => this.checkQuickActions("leave")}  style={{display:"flex", width: this.boxBreakpoints(), flexDirection: "column"}}>
                    <div className="img-wrapper">
                        <div className="lable-block" style={{position:"relative"}}>
                            {this.checkIfNew(product)}
                            {this.checkIfSale(product)}

                        </div>
                        <div className="front" style={{position:"relative"}}>
                            {
                                this.imgBreakPoints(product)
                            }

                            {
                                this.quickActions()
                            }
                            {
                                    this.renderSizes()
                            }

                        </div>
                        <div className="cart-info cart-wrap">

                        </div>
                        {product.variants.length ?
                        <ul className="product-thumb-list" style={{marginBottom:50}}>
                            {this.renderVariants(product)}
                        </ul>
                            :
                            <ul className="product-thumb-list">
                            </ul>
                        }

                    </div>
                    <div className="product-detail">
                        <div>
                            <div className="rating">
                                {RatingStars}
                            </div>
                            <Link to={`/product/${product.id}`}>
                                <h6 style={{textOverflow:"ellipsis", overflow:"hidden", whiteSpace:"nowrap"}}>{product.title}</h6>
                            </Link>
                            <h4>{symbol}{product.variants[0].price}
                                {
                                    product.variants[0].compareAtPrice &&
                                    <span className="money strikethrough" style={{fontSize:10, padding:10, color:"red"}}>{symbol}{product.variants[0].compareAtPrice}</span>
                                }

                            </h4>
                        </div>
                    </div>
                    <Modal open={this.state.open} onClose={this.onCloseModal} center>
                        <div className="modal-dialog modal-lg modal-dialog-centered" role="document">
                            <div className="modal-content quick-view-modal">
                                <div className="modal-body">
                                    <div className="row">
                                        <div className="col-lg-6  col-xs-12">
                                            <div className="quick-view-img">
                                                <img src={this.returnMainIMG(product)} style={{height:100, width:200}} alt="product images" className="img-fluid" />
                                            </div>
                                        </div>
                                        <div className="col-lg-6 rtl-text">
                                            <div className="product-right">

                                                <h2 style={{textOverflow:"ellipsis", overflow:"hidden", whiteSpace:"nowrap"}}> {product.title} </h2>
                                                <h3>{symbol}{product.variants[0].price}</h3>
                                                {/*product.variants?
                                                <ul className="color-variant">
                                                    {product.variants.map((vari, i) => {
                                                        return <li className={vari.color} key={i} title={vari.color} onClick={() => this.onClickHandle(vari.images)}></li>
                                                    })

                                                    }
                                                </ul>:''*/}
                                                <div className="border-product">
                                                    <h6 className="product-title">product details</h6>
                                                    <p>{product.description}</p>
                                                </div>
                                                <div className="product-description border-product my-3">
                                                    {product.size?
                                                    <div className="size-box">
                                                        <ul>
                                                            {product.size.map((size, i) => {
                                                                return <li key={i}><a href={null}>{size}</a></li>
                                                            })}
                                                        </ul>
                                                    </div>:''}
                                                    <h6 className="product-title" style={{marginTop:25, marginBottom:25}}>quantity</h6>
                                                    <div className="qty-box">
                                                        <div className="input-group">
                                                              <span className="input-group-prepend">
                                                                <button type="button" className="btn quantity-left-minus" onClick={this.minusQty} data-type="minus" data-field="">
                                                                 <i className="fa fa-angle-left"></i>
                                                                </button>
                                                              </span>
                                                            <input type="text" name="quantity my-3" value={this.state.quantity}  onChange={this.changeQty} className="form-control input-number" />
                                                                <span className="input-group-prepend">
                                                                <button type="button" className="btn quantity-right-plus" onClick={this.plusQty} data-type="plus" data-field="">
                                                                <i className="fa fa-angle-right"></i>
                                                                </button>
                                                               </span>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="product-buttons">
                                                    <button  className="btn btn-solid" onClick={() => this.handleAddToCart()} >add to cart</button>
                                                    <Link to={`/product/${product.id}`} className="btn btn-solid">view detail</Link>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Modal>
                </div>
        )
    }
}

export default ProductItem
