import React, {Component} from 'react';
import { withTranslate } from 'react-redux-multilingual'
import ReactGA from 'react-ga';
// Custom Components
import HeaderOne from './common/headers/header-one';
import HeaderTwo from './common/headers/header-two';
import HeaderThree from './common/headers/header-three';

import FooterOne from "./common/footers/footer-one";
import FooterTwo from "./common/footers/footer-two";
import FooterThree from "./common/footers/footer-three";

// ThemeSettings
import ThemeSettings from "./common/theme-settings"


let load = false;
class App extends Component {
    constructor() {
        super();
        this.state = {
            location:""
        }
    }

    componentDidMount() {
        ReactGA.initialize('UA-160998529-1');
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (window.location.pathname !== this.state.location) {
            this.setState({location: window.location.pathname}, () => this.updateGApageView())
        }
    }

    updateGApageView = () => {
        ReactGA.pageview(window.location.pathname + window.location.search);
    }

    render() {
        return (
            <div>
                <HeaderOne logoName={'logo.png'}/>
                {this.props.children}
                <FooterOne logoName={'logo.png'}/>

                <ThemeSettings />

            </div>
        );
    }
}

export default withTranslate(App);
