import React, {Component} from 'react';
import {Link} from 'react-router-dom'
import Slider from 'react-slick';
import Modal from 'react-responsive-modal';
import _ from "lodash";
const parse = require("html-react-parser");

class DetailsWithPrice extends Component {

    constructor (props) {
        super (props)
        this.state = {
            open:false,
            quantity:1,
            nav3: null,
            variantAvailable:null,
            sizeAvailable:null,
            variant: "",
            size: null,
            soldOut: false,
        }
    }

    onOpenModal = () => {
        this.setState({ open: true });
    };

    onCloseModal = () => {
        this.setState({ open: false });
    };

    componentDidMount() {
        if (this.props.item.available) {
            this.setState({quantity: 1})

        }
        else {
            this.setState({quantity: 0, soldOut: true})
        }

        this.checkVariantStock(0, this.props.activeVariant);
        if (this.props.activeVariant) {
            const size = _.find(this.props.activeVariant.selectedOptions, (item) => item.name ==="Size")
            if (size) {
                this.checkSizeStock(size.value)
            }
        }


        this.setState({
            nav3: this.slider3
        });
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.activeVariant) {
            if (this.state.variant !== this.props.activeVariant.id) {
                this.setState({variant: this.props.activeVariant.id, quantity: 1})
                this.checkVariantStock(0, this.props.activeVariant);
                if (this.props.activeVariant) {
                    const size = _.find(this.props.activeVariant.selectedOptions, (item) => item.name ==="Size")
                    if (size) {
                        this.checkSizeStock(size.value)
                    }
                }
            }
        }
    }

    minusQty = () => {
        if(this.props.quantity > 1) {
            this.props.setQuantity(this.props.quantity - 1)
        }
    }

    plusQty = () => {
        this.props.setQuantity(this.props.quantity + 1)
    }
    changeQty = (e) => {
        const quantity = parseInt(e.target.value)
        this.props.setQuantity(quantity)
    }

    setVariant = (e) => {
        this.setState({variant: e})
    }

    renderSizes = () => {
        const product = this.props.item
        const variant = this.props.activeVariant
        const variantSizes = [];

        if (product && variant) {
            const findSelectedColor = _.find(variant.selectedOptions, (item) => item.name === "Color")

            if (product.variants.length) {
                for (let i=0; i < product.variants.length; i++) {
                    const variantColor = _.find(product.variants[i].selectedOptions, (item) => item.name === "Color")

                    if (!variantColor) {
                        const variantSize = _.find(product.variants[i].selectedOptions, (item) => item.name === "Size")
                        if (!variantSize) {
                            console.log("skipping one")
                            continue
                        } // this means there are no sizes on this item.
                        if (product.variants[i].available) {
                            variantSizes.push(variantSize.value)
                        }

                    }
                    else if (findSelectedColor) {
                        if (variantColor.value === findSelectedColor.value) {
                            const variantSize = _.find(product.variants[i].selectedOptions, (item) => item.name === "Size")

                            if (product.variants[i].available) {
                                variantSizes.push(variantSize.value)
                            }
                        }
                    }
                    else {
                        continue;
                    }

                }
            }
        }

        if (!variantSizes.length) {
            return <p style={{color:"red", fontSize:14}}>Out of stock of all sizes. Please check back later!</p>
        }

        return variantSizes.map((size, i) => {
            const realSize = size
            return <li key={i} onClick={() => this.checkSizeStock(size)} style={{borderColor: size === this.props.size ? "purple" :"", padding:7, borderRadius:"10%", height:"auto", width:"auto"}}><a style={{fontSize:12}} href={null}>{realSize}</a></li>
        })
    }

    referenceSizeChart=(size)=> {
        const split = size.split("/");
        if (split[0].length < 5) return size;
        switch (split[0]) {
            case "Small":
                return "sm";
            case "Medium":
                return "md";
            case "Large":
                return "lg";
            case "X-Large":
                return "xl";
        }
    }

    checkVariantActive = (product) => {
        if (this.props.variant) {

            if (this.props.variant ===
                product.image.src) {
                return true
            }
            else return false
        }
        else return false
    }

    checkVariantStock = (value, variant) => {
        if (variant) {
            this.props.item.variants.forEach(variant => {
                const sizeLookup = _.find(variant.selectedOptions, item => item.name === "Size")
                const colorLookup = _.find(variant.selectedOptions, item => item.name === "Color")

                const selectedColor = _.find(this.props.activeVariant.selectedOptions, item => item.name === "Color")

                if (colorLookup) {
                    if (selectedColor && sizeLookup && colorLookup) {
                        if (selectedColor.value === colorLookup.value && sizeLookup.value === this.state.size) {
                            if (variant.available) {
                                this.props.setSize(this.state.size)
                                this.props.setVariant(null, variant)
                            }
                        }
                    }
                }
                else if (sizeLookup && this.state.size) {
                    if (sizeLookup.value === this.state.size) {
                        if (variant.available) {
                            this.props.setSize(this.state.size)
                            this.props.setVariant(null, variant)
                        }
                    }
                }
            })
            this.setState({variantAvailable: variant.available})
            this.props.setVariant( value, variant)
        }
    }

    checkStockOfVariant = (item, size, variant) => {

    }

    checkSizeStock = (size) => {
        let sizeCheck = false
        this.props.item.variants.forEach(variant => {
            const sizeLookup = _.find(variant.selectedOptions, item => item.name === "Size")
            const colorLookup = _.find(variant.selectedOptions, item => item.name === "Color")

            const selectedColor = _.find(this.props.activeVariant.selectedOptions, item => item.name === "Color")

            if (colorLookup) {
                if (selectedColor && sizeLookup && colorLookup) {
                    if (selectedColor.value === colorLookup.value && sizeLookup.value === size) {
                        if (variant.available) {
                            this.props.setSize(size)
                            this.props.setVariant(null, variant)
                            sizeCheck = true;
                        }
                    }
                    else {
                        console.log("selectedColor " + selectedColor.value + " colorLookup " + colorLookup.value + " sizeLookup " + sizeLookup.value + " size " + size)
                    }
                }
            }
            else if (sizeLookup) {
                if (sizeLookup.value === size) {
                    if (variant.available) {
                        this.props.setSize(size)
                        this.props.setVariant(null, variant)
                        sizeCheck = true;
                    }
                }
            }
        })

        this.setState({sizeAvailable: sizeCheck, soldOut: sizeCheck})
    }


    renderVariants = (product) => {
        const variantList = [];
        const uniqueList = [];

        for (let a=0; a < product.variants.length; a++) {
            const value = product.variants[a].image.src

            if (uniqueList.indexOf(value) !== -1) continue
            uniqueList.push(value)

            const temp = (
                <li className={`grid_thumb_img`}
                    style={{
                        overflow:"hidden",
                        backgroundColor:"white",
                        borderColor:value === this.props.variant ? "purple" : "",
                        borderWidth: value === this.props.variant ? 2 : 0
                    }}
                    key={a}>
                    <a href="javascript:void(0)" title="Add to Wishlist">
                        <img src={`${product.variants[a].image.src}`}
                             style={{width:25, height: 35, overflow:"hidden"}}
                             onClick={() => this.checkVariantStock(value, product.variants[a])}/>
                    </a>
                </li>
            )
            variantList.push(temp)

        }
        return variantList
    }

    render (){
        const {symbol, item, addToCartClicked, BuynowClicked, addToWishlistClicked} = this.props

        var colorsnav = {
            slidesToShow: 6,
            swipeToSlide:true,
            arrows: false,
            dots: false,
            focusOnSelect: true
        };

        return (
            <div className="col-lg-6 rtl-text">
                <div className="product-right">
                    <p style={{fontFamily:"Poppins", fontSize:24}}> {item.title} </p>
                    <h4>
                        {
                            item.variants[0].compareAtPrice &&
                                <div>
                                    <del>{symbol}{item.variants[0].compareAtPrice}</del>
                                    <span>{Math.round(100- ((item.variants[0].price / item.variants[0].compareAtPrice) * 100))}% off</span>
                                </div>
                        }

                    </h4>
                    <h3>{symbol}{item.variants[0].price} </h3>
                    <div className="product-description border-product">
                        <div>

                            <div className="modal fade" id="sizemodal" tabIndex="-1"
                                 role="dialog" aria-labelledby="exampleModalLabel"
                                 aria-hidden="true">
                                <div className="modal-dialog modal-dialog-centered"
                                     role="document">
                                    <div className="modal-content">
                                        <div className="modal-header">
                                            <h5 className="modal-title"
                                                id="exampleModalLabel">Sheer Straight
                                                Kurta</h5>
                                            <button type="button" className="close"
                                                    data-dismiss="modal" aria-label="Close">
                                                <span aria-hidden="true">&times;</span>
                                            </button>
                                        </div>
                                        <div className="modal-body">
                                            <img src={`/assets/images/size-chart.jpg`} alt="" className="img-fluid"/>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="size-box">
                                <h4>
                                    Sizes
                                </h4>
                                <ul>
                                    {this.renderSizes()}
                                </ul>
                                <h4 style={{marginTop:10}}>
                                    Colors
                                </h4>
                                <ul style={{marginTop:15}}>
                                    {this.renderVariants(item)}
                                </ul>
                            </div>
                        </div>
                        <div className="border-product">
                            <h6 className="product-title my-3">product details</h6>
                            {
                                item.descriptionHtml &&
                                parse(item.descriptionHtml)
                            }

                        </div>
                        <span className="instock-cls">{this.state.variantAvailable ? "" : "Out of Stock"}</span>
                        <h6 className="product-title" style={{marginTop:25, marginBottom:10}}>quantity</h6>
                        <div className="qty-box">
                            <div className="input-group">
                                  <span className="input-group-prepend">
                                    <button type="button" className="btn quantity-left-minus" onClick={this.minusQty} data-type="minus" data-field="">
                                     <i className="fa fa-angle-left"></i>
                                    </button>
                                  </span>
                                <input type="text" name="quantity" value={this.props.quantity} onChange={this.changeQty} className="form-control input-number" />
                                <span className="input-group-prepend">
                                <button type="button" className="btn quantity-right-plus" onClick={this.plusQty} data-type="plus" data-field="">
                                <i className="fa fa-angle-right"></i>
                                </button>
                               </span>
                            </div>
                        </div>
                    </div>

                    <div className="product-buttons" >
                        <button className="btn btn-solid d-block my-3 mx-auto"  style={{width:"60%"}} disabled={this.props.activeVariant ? !this.props.activeVariant.available : true} onClick={() => addToCartClicked(item, this.props.quantity)}>add to cart</button>
                        <button className="btn btn-solid d-block my-3 mx-auto" style={{width:"60%"}} disabled={this.props.activeVariant ? !this.props.activeVariant.available : true}onClick={() => BuynowClicked(item, this.state.quantity, true)} >buy now</button>
                    </div>

                    <div className="border-product">
                        <h6 className="product-title my-3">share it</h6>
                        <div className="product-icon">
                            <ul className="product-social">
                                <li><a href="https://www.facebook.com/" target="_blank"><i className="fa fa-facebook"></i></a></li>
                                <li><a href="https://plus.google.com/discover" target="_blank"><i className="fa fa-google-plus"></i></a></li>
                                <li><a href="https://twitter.com/" target="_blank"><i className="fa fa-twitter"></i></a></li>
                                <li><a href="https://www.instagram.com/" target="_blank"><i className="fa fa-instagram"></i></a></li>
                            </ul>
                                <button className="wishlist-btn" onClick={() => addToWishlistClicked(item)}><i
                                    className="fa fa-heart"></i><span
                                    className="title-font">Add To WishList</span>
                                </button>
                        </div>
                    </div>
                    {/*<div className="border-product">
                        <h6 className="product-title">Time Reminder</h6>
                        <div className="timer">
                            <p id="demo">
                                <span>25
                                    <span className="padding-l">:</span>
                                    <span className="timer-cal">Days</span>
                                </span>
                                <span>22
                                    <span className="padding-l">:</span>
                                    <span className="timer-cal">Hrs</span>
                                </span>
                                <span>13
                                    <span className="padding-l">:</span>
                                    <span className="timer-cal">Min</span>
                                </span>
                                <span>57
                                    <span className="timer-cal">Sec</span>
                                </span>
                            </p>
                        </div>
                    </div>*/}
                </div>
            </div>
        )
    }
}


export default DetailsWithPrice;
