const TIMEOUT = 350





export default {
    getProducts:async (cb, timeout) => setTimeout(async () => {
        const items = []
        cb(items);
    }, timeout || TIMEOUT),
    getCollections:async (cb, timeout, endpoint) => setTimeout(async () => {
        const items = []
        cb(items);
    }, timeout || TIMEOUT),
    buyProducts: (payload, cb, timeout) => setTimeout(() => cb(), timeout || TIMEOUT)
}

