import React, {Component} from 'react';
import {Helmet} from 'react-helmet'
import Slider from 'react-slick';
import '../common/index.scss';
import {connect} from "react-redux";

// import custom Components
import Service from "./common/service";
import BrandBlock from "./common/brand-block";
import NewProduct from "../common/new-product";
import Breadcrumb from "../common/breadcrumb";
import DetailsWithPrice from "./common/product/details-price";
import DetailsTopTabs from "./common/details-top-tabs";
import {addToCart, addToCartUnsafe, addToCompare, addToWishlist, searchSingle} from '../../actions'
import ImageZoom from './common/product/image-zoom'
import SmallImages from './common/product/small-image'
import {Redirect} from "react-router-dom";
import ProductItem from "../layouts/common/product-item";
import {getBestSeller, getUnderFifteen, getVisibleproducts, getWomensWear} from "../../services";
import Carousel from "react-multi-carousel";
import {Container} from "react-bootstrap";

import 'react-multi-carousel/lib/styles.css';
import _ from "lodash";


const responsive = {
    superLargeDesktop: {
        breakpoint: { max: 4000, min: 3000 },
        items: 8
    },
    desktop: {
        breakpoint: { max: 3000, min: 2048 },
        items: 7
    },
    smallDesktop: {
        breakpoint: { max: 2048, min: 1024},
        items: 4
    },
    tablet: {
        breakpoint: {max: 1024, min: 767},
        items: 3
    },
    smallTablet: {
        breakpoint: { max: 767, min: 464 },
        items: 2
    },
    mobile: {
        breakpoint: { max: 464, min: 0 },
        items: 2
    }
};

function NextArrow(props) {
    const { className, style, onClick } = props;
    return (
        <div
            className={className}
            style={{ ...style, display:"absolute", top:"100%", bottom:0, left:"50%", transform:"translate(-50%, 0px) rotate(90deg)" }}
            onClick={onClick}
        />
    );
}

function PrevArrow(props) {
    const { className, style, onClick } = props;
    return (
        <div
            className={className}
            style={{ ...style, display:"absolute", top:-10, left:"50%", transform:"translate(-50%, 0px) rotate(90deg)" }}
            onClick={onClick}
        />
    );
}

class LeftSideBar extends Component {

    constructor() {
        super();
        this.state = {
            open:false,
            variant: null,
            size: null,
            quantity: 1,
            nav1: null,
            nav2: null,
            activeVariant:"",
            productID: null,
            product : null,
            vertical: false
        };
    }

    // document.getElementById('idOfElement').classList.add('newClassName');

    // add to cart requires a size literal & an object called variant

    componentDidMount() {
        this.getProductSizeAndColor()

        if (window.innerWidth > 576) {
            this.setState({vertical:true})
        }else{
            this.setState({vertical:false})
        }

        this.setState({
            nav1: this.slider1,
            nav2: this.slider2,
        });
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.item) {
            if (this.state.productID !== this.props.item.id) {
                this.setState({productID: this.props.item.id})
                this.getProductSizeAndColor()
            }
        }
    }

    getProductSizeAndColor = async () => {
        const product = this.props.item
        let sizeMatch = false;
        let colorMatch = false;
        let activeVariant = false;

        try {
            this.setState({product: product.id})
            for await (const obj of product.variants[0].selectedOptions) {
                if (obj.name === "Color") {
                    activeVariant = product.variants[0].image.src
                    colorMatch = product.variants[0]
                }
                if (obj.name === "Size") {
                    sizeMatch =  obj.value
                }
            }
            if (!colorMatch) {
                colorMatch = product.variants[0];
                activeVariant = product.variants[0].images[0].image.src
            }
            if (!sizeMatch) {
                sizeMatch = "One Size"
            }

        }
        catch (e) {
            console.log("An error has occurred! Check Left Sidebar function getProductsSizeAndColor Additional logging will be below")
            console.log(e)
        }

        console.log("_____________________active variant")
        console.log(colorMatch)
        console.log("___________________________________")
        console.log(product)
        this.setState({
            variant: colorMatch,
            size: sizeMatch,
            activeVariant: activeVariant
        })

    }

    filterClick() {
        document.getElementById("filter").style.left = "-15px";
    }
    backClick() {
        document.getElementById("filter").style.left = "-365px";
    }

    renderVariants = () => {
        const item = this.props.item;
        const imgBuilder = []
        const uniqueImages = []
        try {
            for(let i=0; i < item.images.length; i++) {
                if (uniqueImages.indexOf(item.images[i].src) === -1) {
                    uniqueImages.push(item.images[i].src)
                    const temp = item.images[i].src;
                    imgBuilder.push(temp)
                }
            }
            const builder = [];
            const sortByActive = [];
            //sort image by active variant
            const active = imgBuilder.filter((item) => item === this.state.activeVariant)
            sortByActive.push(active)

            for (let i=0; i < imgBuilder.length; i++) {
                if (imgBuilder[i] === active) continue;
                sortByActive.push(imgBuilder[i])
            }

            //builds variants from sorted list
            for (let i=0; i < sortByActive.length; i++) {
                const temp = (
                    <div key={i}>
                        <ImageZoom image={sortByActive[i]} />
                    </div>
                )
                builder.push(temp)
            }

            return builder;
        }
        catch(e) {
            const errorMsg = {
                type: "Error at rendering components",
                location:"product view",
                error: e,
                product: item
            }
            console.log(errorMsg)
            return []
        }
    }

    handleVariantClick = (item, variant) => {
        const img = variant.image.src
        this.slider1.slickGoTo(0, false);
        this.setState({variant: variant, activeVariant: img})
    }


    handleAddToCart = async (item, quantity, buyNow) => {
        this.setState({quickBuy: false, quickAdd:false})
        const product = item;
        const variant = this.state.variant;

        let selectedColor;
        let selectedSize = this.state.size

        if (selectedSize.includes("/")) {
            selectedSize = selectedSize.split("/")[0];
        }

        //get variant value
        try {
            for await (const options of variant.selectedOptions) {
                if (options.name === "Color") {
                    selectedColor = options.value;
                }
            }
        }
        catch {}

        let endProduct = null;
        product.variants.forEach(item => {
            const searchColor = _.find(item.selectedOptions, (item) => item.name === "Color")
            const searchSize = _.find(item.selectedOptions, (item) => item.name === "Size")

            if (!searchColor) {
                if (!searchSize) {
                    console.log("No color or sizes exist within this item!")
                    endProduct = product
                    return product;
                }
                else {
                    let newSearchSize = searchSize.value
                    if (searchSize.value.includes("/")) {
                        newSearchSize = searchSize.value.split("/")[0];
                    }
                    if (newSearchSize === selectedSize) {
                        console.log("size match found//no colors exists with this item.")
                        endProduct = item;
                        return item;
                    }
                    else {
                        let newSearchSize = searchSize.value
                        if (searchSize.value.includes("/")) {
                            newSearchSize = searchSize.value.split("/")[0];
                        }
                        if (newSearchSize === selectedSize) {
                            console.log("Good News! We found your item!")
                            console.log("item id is" + item.id)
                            endProduct = item;
                            return item;
                        }
                    }
                }
            }
            else {
                if (!searchSize) {
                    console.log("No sizes exist within this item!")
                    if (searchColor.value === selectedColor) {
                        endProduct = item;
                        return item;
                    }
                }
                else {
                    let newSearchSize = searchSize.value
                    if (searchSize.value.includes("/")) {
                        newSearchSize = searchSize.value.split("/")[0];
                    }

                    if (searchColor.value === selectedColor && newSearchSize === selectedSize) {
                        console.log("Good News! We found your item!")
                        console.log("item id is" + item.id)
                        endProduct = item;
                        return item;
                    }
                }
            }
        })

        if (!endProduct) {
            console.log("error at")
            console.log(product)
        }
        else {
            if (buyNow) {
                this.props.client.client.checkout.create().then(checkout => {
                    let lineItemsToAdd
                    try {
                        lineItemsToAdd = [
                            {
                                variantId: endProduct.id,
                                quantity: quantity,
                            }
                        ];

                        this.props.client.client.checkout.addLineItems(checkout.id, lineItemsToAdd).then((checkout) => {
                            console.log(checkout)
                           window.open(checkout.webUrl)
                        });
                    }
                    catch {
                        console.log("error with product listed below")
                        console.log(product)
                    }

                })
            }
            else {
                this.props.addToCart(endProduct, quantity, this.props.client)
            }

        }

    }


    render(){
        const {symbol, item, addToCart, addToCartUnsafe, addToWishlist} = this.props
        var products = {
            slidesToShow: 1,
            slidesToScroll: 1,
            dots: false,
            arrows: true,
            fade: true
        };
        var productsnav = {
            vertical: true,
            slidesToScroll: 1,
            slidesToShow: 4,
            arrows: true,
            infinite: true,
            dots: false,
            nextArrow: <NextArrow/>,
            prevArrow: <PrevArrow/>,
            focusOnSelect: false,
            responsive: [
                {
                    breakpoint: 1024,
                    settings: {
                        slidesToShow: 4,
                        slidesToScroll: 1
                    }
                },
                {
                    breakpoint: 767,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 1
                    }
                },
                {
                    breakpoint: 578,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 1
                    }
                }
            ]
        };

        if (!item) {
            return <div></div>
        }

        return (
            <div>
                {/*SEO Support*/}
                <Helmet>
                    { item ?
                        <title>Pretty Girl Store | {item.productType} | {item.title}</title>
                        :
                        <title>Pretty Girl Store</title>
                    }

                    <meta name="description" content="Pretty Girl have offered incredible designs, stylish clothes, and magnificent modern looks for the budget minded fashionista. Serving our adoring customers in any one of our 25 Pretty Girl Stores throughout the Tri-State area, Pretty Girl prides itself on taking fast fashion to the next level at incredibly low prices." />
                </Helmet>
                {/*SEO Support End */}

                <Breadcrumb  parent={'Product'} />

                {/*Section Start*/}
                {(item)?
                <section className="section-b-space">
                    <div className="collection-wrapper">
                        <div className="container">
                            <div className="row">

                                <div className="col-sm-3 collection-filter" id="filter">
                                    <div  className="collection-mobile-back pl-5">
                                        <span onClick={this.backClick}  className="filter-back">
                                            <i className="fa fa-angle-left" aria-hidden="true"></i> back
                                        </span>
                                    </div>

                                    {/* <BrandBlock/> */}
                                    <Service/>
                                    {/*side-bar single product slider start*/}
                                    <NewProduct/>
                                    {/*side-bar single product slider end*/}
                                </div>
                                <div className="col-lg-9 col-sm-12 col-xs-12">
                                    <div className="">
                                        <div className="row">
                                            <div className="col-lg-1 col-sm-2 col-xs-12 p-0">
                                                {
                                                    this.props.window.width > 576 &&
                                                        <SmallImages
                                                            setVariant={(item, variant) => this.handleVariantClick(item, variant)}
                                                            variant={this.state.variant}
                                                            item={item} settings={productsnav} navOne={this.state.nav1}/>
                                                }
                                            </div>

                                            <div className="col-lg-5 col-sm-10 col-xs-12  order-up">
                                                <Slider {...products} asNavFor={this.state.nav2} ref={slider => (this.slider1 = slider)} className="product-right-slick">
                                                    {
                                                        this.renderVariants()
                                                    }
                                                </Slider>

                                            </div>
                                            <DetailsWithPrice
                                                setSize={(size) => this.setState({size: size})}
                                                setVariant={(item, variant) => this.handleVariantClick(item, variant)}
                                                variant={this.state.activeVariant} activeVariant={this.state.variant}
                                                size={this.state.size} quantity={this.state.quantity}
                                                setQuantity={(quantity) => this.setState({quantity: quantity})} symbol={symbol}
                                                item={item} navOne={this.state.nav1} addToCartClicked={this.handleAddToCart}
                                                BuynowClicked={this.handleAddToCart} addToWishlistClicked={addToWishlist} />
                                        </div>
                                    </div>
                                    {/*<DetailsTopTabs item={item} />*/}
                                </div>
                            </div>
                        </div>
                    </div>
                </section> : ''}
                <Carousel responsive={responsive}>
                { this.props.bestSeller.map((product, index ) =>
                    <div key={index} className={"mx-2"}>
                        <ProductItem product={product} symbol={symbol}
                                     onAddToCompareClicked={() => addToCompare(product)}
                                     onAddToWishlistClicked={() => addToWishlist(product)}
                                     onAddToCartClicked={(item) => addToCart(item, 1, this.props.client)} key={index} window={this.props.window} />
                    </div>)

                }
                </Carousel>
                {/*Section End*/}
            </div>
        )
    }
}




const mapStateToProps = (state, ownProps) => {
    let productId = ownProps.match.params.id;
    return {
        bestSeller: getBestSeller(state.data.products),
        item: state.data.products.find(el => el.id == productId),
        symbol: state.data.symbol,
        client: state.client,
        window: state.window.window
    }
}

export default connect(mapStateToProps, {addToCart, addToCartUnsafe, addToWishlist, searchSingle}) (LeftSideBar);
