import React, {Component, useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import {connect} from 'react-redux';

import CartPage from '../components/common/headers/common/cart-header'
import {removeFromCart, openCartSidebar} from '../actions'
import {getCartTotal} from '../services'

const CartContainer = ({cartList, total, symbol, removeFromCart, client, cartSidebar, openCartSidebar, window}) => {
    useEffect(() => {
        //component will mount
        return () => {
            //component will unmount
        };
    }, [total]);

    return (
        <li  className="onhover-div mobile-cart" style={{paddingLeft:0, flex:1, flexShrink:0, marginLeft:"2vw", marginRight:"2vw",zIndex:5}} onClick={()=> openCartSidebar(true)}><div className="cart-qty-cls">{cartList.products ? cartList.products.length : null}</div>
            <div>
                <img src={`/assets/images/icon/cart.png`} style={{}} className="img-fluid" alt=""/>
                <i className="fa fa-shopping-cart"></i>
                {/*<ul className="show-div shopping-cart">
                    { cartList.products.map((item,index) => (
                        <CartPage key={index} item={item} total={total} symbol={symbol} removeFromCart={() => removeFromCart(item, client)}  />
                    ))}
                    {(cartList.products.length > 0) ?
                        <div>
                            <li>
                                <div className="total">
                                    <h5>subtotal : <span>{symbol}{cartList.sum}</span></h5>
                                </div>
                            </li>
                            <li>
                                <div className="buttons">
                                    {/*<Link to={`${process.env.PUBLIC_URL}/cart`} className="view-cart">view cart</Link>
                                    <p onClick={createCheckout} className="checkout">Checkout</p>
                                </div>
                            </li></div>
                        :
                        <li><h5>Your cart is currently empty.</h5></li>}
                        </ul>
                        */}

            </div>
        </li>
    )
}


function mapStateToProps(state) {
    return {
        cartList: state.cartList,
        symbol: state.data.symbol,
        client: state.client,
        cartSidebar: state.cartSidebar.open,
        window: state.window.window
    }
}

export default connect(mapStateToProps, {removeFromCart, openCartSidebar})(CartContainer);
