
import React, { Component } from 'react';
import Slider from 'react-slick';
import ImageZoom from "./image-zoom";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import _ from "lodash";

class SmallImages extends Component {
    constructor (props) {
        super(props);
        this.state = {
            nav2: null,
            variant: null
        }
    }
    componentDidMount() {
        this.setState({
            nav2: this.slider2
        });
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.variant) {
            if (this.state.variant !== this.props.variant.id) {
                this.setState({variant: this.props.variant.id})
                this.goToSlide();
            }
        }

    }

    colorAndSizeAvailable = (colorSelect, variantColor, sizeSelect, variantSize) => {
        if (colorSelect.value === variantColor.value) {
            return true;
        }
        return false;

    }

    onlyColorAvailable = (colorSelect, variantColor) => {
        if (colorSelect.value === variantColor.value) {
            return true;
        }
        return false;
    }

    onlySizeAvailable = (sizeSelect, variantSize) => {
        if (sizeSelect.value === variantSize.value) {
            return true;
        }
        return false;
    }

    noOptionsAvailable = (variant) => {
        return false;
    }

    goToSlide = () => {
        const item = this.props.item;
        let selectedVariant = null;

        for (let i = 0; i < item.variants.length; i++) {
            if (this.props.variant && item.variants[i]) {
                const colorSelect = _.find(this.props.variant.selectedOptions, (item) => item.name === "Color")
                const sizeSelect = _.find(this.props.variant.selectedOptions, (item) => item.name === "Size")

                const variantColor = _.find(item.variants[i].selectedOptions, (item) => item.name === "Color");
                const variantSize = _.find(item.variants[i].selectedOptions, (item) => item.name === "Size");


                if (colorSelect && variantColor) {
                    if (sizeSelect && variantSize) {
                        selectedVariant = this.colorAndSizeAvailable(colorSelect, variantColor, sizeSelect, variantSize)
                    } else {
                        selectedVariant = this.onlyColorAvailable(colorSelect, variantColor)
                    }
                } /*else {
                    if (sizeSelect && variantSize) {
                        selectedVariant = this.onlySizeAvailable(sizeSelect, variantSize)
                    } else {
                        selectedVariant = this.noOptionsAvailable()
                    }
                }*/
            }
            if (selectedVariant) {
                for (let b=0; b < item.images.length; b++) {
                    if (item.variants[i].image.src === item.images[b].src) {
                        this.slider2.slickGoTo(b, false)
                        return;
                    }
                }
                return;
            }
        }
    }

    renderVariants = () => {
        const item = this.props.item;
        const imgBuilder = [];

        try {

            if (item) {
                for (let i = 0; i < item.images.length; i++) {
                    const temp = <div onClick={() => this.setVariant(item.images[i].src)} >
                        <ImageZoom style={{cursor: "pointer"}} image={item.images[i].src} />
                    </div>

                    imgBuilder.push(temp)
                }
            }
            return imgBuilder;
        }
        catch(e) {
            console.log("error in small-image component")
            console.log(e)
            return []
        }
    }

    render() {
        const { item, settings } = this.props;

        var productsnav = settings;

        return (
            <div className="row">
                <div className="col-12 p-0">
                    <Slider {...productsnav}  ref={slider => (this.slider2 = slider)} className="p-3">
                        {
                            this.renderVariants()
                        }
                    </Slider>
                </div>
            </div>
        );
    }
}

export default SmallImages;
