import React, { Component } from 'react';
import Slider from 'react-slick';

import {Slider6} from "../../../services/script";
import {getCartTotal} from "../../../services";
import {connect} from "react-redux";
import {decrementQty, incrementQty, openCartSidebar, removeFromCart} from "../../../actions";

class LogoBlock extends Component {

    render (){
        return (
            <section className="section-b-space">
                <div className="container">
                    <div className="row">
                        <div className="col-md-12">
                            <Slider {...Slider6} className="slide-6 no-arrow">
                                <div>
                                    <div className="logo-block">
                                        <a href={null}>
                                            <img style={{width: this.props.window.width < 900 ? "5rem" : "15rem"}} src={`/assets/images/logos/forever21.png`} alt="" />
                                        </a>
                                    </div>
                                </div>
                                <div>
                                    <div className="logo-block">
                                        <a href={null}>
                                            <img style={{width: this.props.window.width < 900 ? "5rem" : "15rem"}} src={`/assets/images/logos/juicy-culture.png`} alt="" />
                                        </a>
                                    </div>
                                </div>
                                <div>
                                    <div className="logo-block">
                                        <a href={null}>
                                            <img  style={{width: this.props.window.width < 900 ? "5rem" : "15rem"}} src={`/assets/images/logos/charlotte.png`} alt="" />
                                        </a>
                                    </div>
                                </div>
                            </Slider>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}

const mapStateToProps = (state) => ({
    window: state.window
})

export default connect(
    mapStateToProps,
)(LogoBlock)

